[v-cloak] {
  display: none;
}

.croppr-container {
  display: flex;
  justify-content: center;
  padding: 1rem;
}

.label .is-dashed {
  border-bottom: 1px dashed #b5b5b5;
  cursor: default;
}

@media screen and (max-width: 1215px) {
  .navbar .container {
    padding-left: 0;
    padding-right: 0;
  }
}

.secondary-navbar {
  position: relative;
  overflow-x: auto;
  min-height: 3.25rem;
  display: flex;
  align-items: center;
}

.secondary-navbar .container {
  width: 100%;
}

.secondary-navbar .breadcrumb {
  overflow-x: auto;
  display: flex;
  align-items: center;
}

.secondary-navbar .breadcrumb ul {
  flex-wrap: nowrap;
}

.secondary-navbar .level {
  width: 100%;
}

@media screen and (max-width: 1215px) {
  .secondary-navbar .container {
    padding: 1rem 1rem;
  }
}

.is-in-container .tabs {
  margin-left: -1.5rem;
  margin-right: -1.5rem;
}

.is-danger .table thead tr:first-child {
  background-color: #ff3860;
}

.is-danger .table thead tr:first-child th {
  color: #fff;
}

@media screen and (min-width: 769px)and (max-width: 1023px) {
  .button.has-text-hidden-tablet-only .icon:first-child {
    margin-right: 0;
    margin-left: 0;
  }
}

.panel-heading .b-checkbox {
  font-size: 1rem;
}

.message-actions :not(:last-child) {
  margin-right: 1rem;
}

.icon.is-small .fa {
  font-size: 1rem;
}

.photo-upload {
  width: 128px;
  height: 128px;
}

.modal-action {
  user-select: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background-color: rgba(10,10,10,.2);
  border: 1px solid #fff;
  border-radius: 290486px;
  cursor: pointer;
  display: inline-block;
  -webkit-box-flex: 0;
  -ms-flex-positive: 0;
  flex-grow: 0;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  outline: none;
  position: relative;
  vertical-align: bottom;
  text-align: center;
  background: none;
  position: fixed;
  right: 20px;
  bottom: 20px;
  height: 32px;
  max-height: 32px;
  max-width: 32px;
  min-height: 32px;
  min-width: 32px;
  width: 32px;
  color: #fff;
}

.modal-action .icon {
  position: absolute;
  right: 3px;
  bottom: 4px;
}

.pagination.is-species .pagination-title,
.pagination.is-species .pagination-search {
  align-items: baseline;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;
}

@media screen and (min-width: 769px),print {
  .pagination.is-species .pagination-title,
  .pagination.is-species .pagination-search {
    flex-grow: 1;
    flex-shrink: 1;
    justify-content: flex-start;
    order: 1;
